.FAQ {
    position: relative;
    width: 100%;
    height: 60rem;
    background-image: linear-gradient(to top, #3b41c5 0%, #a981bb 49%, #ffc8a9 100%);
    display: flex;
    flex-direction: column;
}

.title {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    font-family: 'Manrope', sans-serif;
    font-size: 1.7em;
    color: #fff;
    position: relative;
    margin: 1em 0 0 0;
}
.title::after {
    content: '';
    position: absolute;
    height: 6px;
    width: 130px;
    background: #fff;
    border-radius: 15px;
    top: 55%;
    transform: translate(0%, 0%);
    margin: .6em 0 0 0;
}

.mainFAQ {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    background: #fff; 
}
.innerFAQ {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}
.leftFAQ:hover > .bulletFAQ {
    animation: faq_load 1200ms ease-out forwards;
}
.leftFAQ {
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
}
.bulletFAQ {
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 0 0 5%;
    font-family: 'Manrope', sans-serif;
    justify-content: center;
    text-align: left;
    color: rgb(11, 11, 68);
    margin: 3% 10% 10% 8%;
    transform: translate(-120%, 0%);
}
.bulletFAQ > h1 {

}
.bulletFAQ > p {
    font-size: 1.2vw;
    padding: .5% 0 5% 0;
}
.rightFAQ {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 30%;
}

@keyframes faq_load {
    0% {
        transform: translate(-120%, 0%);
    }
    100% {
        transform: translate(0%, 0%);
    }
}