
.Main {
    background-image: linear-gradient(to bottom, #3b41c5 0%, #a981bb 49%, #ffc8a9 100%);
    height: 91vh;
    width: 100%;
    display: flex;
    overflow: hidden;
}

.heroLeft {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 45%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-105%, -68%);
    margin: 4% 1% 1% 1%;
    animation: left-load 1600ms ease-out;
}

.heroLeft > h1 {
    font-family: 'Rubik', sans-serif;
    font-weight: normal;
    font-size: 8rem;
    color: #fff;
}

.tryBut {
    position: relative;
    animation: try-scale 1700ms ease-in-out 1000ms;
    height: 70px;
    padding: 0.4rem 1rem;
    background: #ffffff00;
    border: 2px solid #fafafa;
    font-size: 1.2rem;
    color: #fff;
    line-height: 1;
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    margin-top: 20px;
    font-family: 'Rubik', sans-serif;
}
.tryBut:hover {
    border: 2px solid #040668;
    color: #040668;
}

.heroRight {
    height: 100%;
    width: 45%;
    position: relative;
    right: 50%;
    transform: translate(223%, 0%);
    margin: 0 1% 1% 1%;
    animation: right-load 2800ms ease-in-out;
    display: flex;
    flex-direction: row;
}

.lbot {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 600px;
    width: 330px;
    background: rgba(255, 255, 255, 0);
    margin: 0;
    bottom: 0%;
    transform: translate(3%, 100%);
    z-index: 2;
}
.llid {
    display: flex;
    justify-content: center;
    position: relative;
    height: 100px;
    width: 100%;
}
.ltop {
    position: absolute;
    height: 30px;
    width: 90%;
    background: rgb(236, 236, 236);
}
.lbottom {
    position: absolute;
    height: 95px;
    width: 100%;
    background: rgb(224, 224, 224);
    top: 13%;
    border-radius: 15px 15px 0 0;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.lbottom > span {
    height: 68px;
    width: 20px;
    background: rgb(207, 207, 207);
    border-radius: 15px;
    margin: 1.8%;
}

.lrest {
    display: flex;
    justify-content: center;
}
.llower {
    height: 515px;
    width: 85%;
    background: rgb(255, 157, 0);
    display: flex;
    align-items: center;
    border-radius: 0 0 15px 15px;
    box-shadow: inset 0em 2.5em 0em 0em rgba(0, 0, 0, 0.068);
}
.lmid {
    height: 325px;
    width: 100%;
    background: #fff;
}

.lupMid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60%;
    font-family: 'Lobster', cursive;
    font-size: 1.7em;
    color: rgb(41, 38, 30);
}
.bottle {
    position: relative;
    display: flex;
    flex-direction: column;
    height: 600px;
    width: 330px;
    background: rgba(255, 255, 255, 0);
    bottom: 0%;
    transform: translate(10%, 105%);
    z-index: 1;
}
.lid {
    display: flex;
    justify-content: center;
    position: relative;
    height: 100px;
    width: 100%;
}
.top {
    position: absolute;
    height: 30px;
    width: 90%;
    background: rgb(236, 236, 236);
    box-shadow: inset 5em 0em rgb(219, 219, 219);
}
.bottom {
    position: absolute;
    height: 95px;
    width: 100%;
    background: rgb(224, 224, 224);
    top: 13%;
    border-radius: 15px 15px 0 0;
    display: flex;
    flex-direction: row;
    align-items: center;
    box-shadow: inset 7em 0em rgba(212, 212, 212, 0.747)
}
.bottom > span {
    height: 68px;
    width: 20px;
    background: rgb(207, 207, 207);
    border-radius: 15px;
    margin: 1.8%;
}

.rest {
    display: flex;
    justify-content: center;
}
.lower {
    height: 515px;
    width: 85%;
    background: rgb(255, 157, 0);
    display: flex;
    align-items: center;
    border-radius: 0 0 15px 15px;
    box-shadow: inset 4em 2.5em 0em 0em rgba(0, 0, 0, 0.068);
}
.mid {
    height: 325px;
    width: 100%;
    background: #fff;
    box-shadow: inset 4em 0em 0em 0em rgba(0, 0, 0, 0.068);
}

.upMid {
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60%;
    font-family: 'Lobster', cursive;
    font-size: 1.6em;
    color: rgb(41, 38, 30);
}
.lowMid {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 40%;
    font-family: 'Gowun Dodum', sans-serif;
    font-size: .8em;
    text-align: left;
    text-indent: 10px;
}

@keyframes left-load {
    0% {
        transform: translate(-405%, -68%);
    }
    100% {
        transform: translate(-105%, -68%);
    }
}

@keyframes right-load {
    0% {
        transform: translate(223%, 285%);
    }
    100% {
        transform: translate(223%, 0%);
    }
}

@keyframes try-scale {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}

