.Products {
    width: 100%;
    height: 75rem;
    background: rgb(26, 22, 36);
    display: flex;
    flex-direction: row;
    
}
.lToggle {
    height: 100%;
    width: 100%;
    background: rgb(247, 246, 239);
    position: absolute;
    z-index: 3;
    transition: transform 1s ease-in-out;
}
.leftHero:hover > .lToggle {
    transform: translateX(100%); 
}

.leftHero {
    display: flex;
    height: 100%;
    width: 50%;
    background: #796cc2;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    position: relative;
    z-index: 1;
    border: 1px solid rgb(180, 180, 180);
}

.leCard {
    height: 620px;
    width: 300px;
    background: rgba(241, 240, 235, 0);
    border-radius: 5px 5px 5px 5px;
    position: absolute;
    z-index: 4;
    box-shadow: 15px 20px 70px 50px rgba(0, 0, 0, 0.13);
}
.leTop {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 140px;
    background: #aea0ffde;
    border-radius: 5px 5px 0 0;
    font-family: "Rubik", sans-serif;
    font-size: .65em;
    font-weight: normal;
    color: rgb(24, 24, 24);
}
.leMid {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 400px;
    background: rgba(241, 240, 235, 0.705);
    font-family: 'Lobster', cursive;
    font-size: 1.7em;
    color: rgb(41, 38, 30);
}
.leBot {
    height: 80px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    font-family: 'Gowun Dodum', sans-serif;
    text-align: left;
    text-indent: 20px;
    border-radius: 0 0 5px 5px;
    background: rgba(241, 240, 235, 0.705);
}
.leBot > h1 {
    font-size: 1.4em;
}
.leBot > p {
    font-size: .8em;
    font-weight: bold;
}
.leBot > p:nth-child(3) {
    padding-bottom: 10px;
}

.health {
    height: 100px;
    width: 60%;
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 20vh;
    font-family: "Rubik", sans-serif;
    color: #fff;
    z-index: 1;
}

.health > span {

}
.leftHero:hover > .health span:nth-child(1) {
    animation: h1 3000ms ease-in-out forwards infinite alternate;
   
}
.leftHero:hover > .health span:nth-child(2) {
    animation: h2 3000ms ease-in-out forwards infinite alternate;
    
}
.leftHero:hover > .health span:nth-child(3) {
    animation: h3 3000ms ease-in-out forwards infinite alternate;
    
}
.leftHero:hover > .health span:nth-child(4) {
    animation: h4 3000ms ease-in-out forwards infinite alternate;
    
}
.leftHero:hover > .health span:nth-child(5) {
    animation: h5 3000ms ease-in-out forwards infinite alternate;
    
}
.leftHero:hover > .health span:nth-child(6) {
    animation: h6 3000ms ease-in-out forwards infinite alternate;
    
}

