@keyframes h1 {
    0% {
        transform: rotate(0) translateX(0);
    }
    100% {
        transform: rotate(-35deg) translateX(-5%);
    }
}
@keyframes h2 {
    0% {
        transform: rotate(0) translateY(0) translateX(0);
    }
    100% {
        transform: rotate(-28deg) translateY(-26%) translateX(0%);
    }
}
@keyframes h3 {
    0% {
        transform: rotate(0) translateY(0) translateX(0);
    }
    100% {
