
.Footer {
    position: absolute;
    height: 35rem;
    width: 100%;
    z-index: 5;
    background: #3b41c5;
    display: flex;
    flex-direction: row;
}

.leftSide {
    height: 100%;
    width: 70%;
    display: flex;
    flex-direction: column;
}
.lUpper {
    height: 40%;
    width: 100%;
    display: flex;
    flex-direction: row;
    font-family: 'Manrope', sans-serif;
}
.leftUp {
    height: 100%;
    width: 33.3%;
    display: flex;
    flex-direction: column;
    padding: 50px 0 0 0;
}
.leftUp > a:hover, .midUp > a:hover {
    color: rgb(12, 11, 49);
}
.leftUp > a, .midUp > a {
    transition: color 150ms ease;
}
.midUp {
    height: 100%;
    width: 33.3%;
    display: flex;
    flex-direction: column;
    padding: 50px 0 0 0;
}
.rightUp {
    height: 100%;
    width: 33.3%;
    display: flex;
    flex-direction: column;
    padding: 50px 0 0 0;
}
.leftUp, .midUp, .rightUp > h1 {
    color: #fff;
}
a {
    margin: 15px 0 0 0;
    color: rgb(255, 255, 255);
    text-decoration: none;
}

.lLower {
    height: 60%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    font-family: 'Manrope', sans-serif;
    color: rgb(255, 255, 255);
}
.upUpper {
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-family: 'Manrope', sans-serif;
    color: rgb(255, 255, 255);
    margin: 0 0 0 9%;
}
.lowUpper {
    height: 0%;
    width: 100%;
    display: flex;
    align-items: center;
    margin: 0 0 0 9%;
}

.buttonBox {
    border-radius: 1.5rem;
    border: 0px solid #e3e3e3;
    overflow: hidden;
}
.signUpForm {
    width: 400px;
    display: flex;
    flex-direction: row;
    position: relative;
    border-radius: 1.5rem;
    font-family: 'Manrope', sans-serif;
}
.news_input {
    width: 100%;
    background: 0 0;
    border: 1px solid #e3e3e3;
    border-radius: 1.5rem;
    font-size: 1.6rem;
    line-height: 81%;
    color: #fff;
    padding: 1.5rem 6rem 1.5rem 2.3rem;
    font-family: lausanne,sans-serif;
    transition: 200ms ease;
}
.news_input:focus {
    outline-color: rgb(34, 33, 78);
    box-shadow: 0 0 0 4px rgba(21, 156, 228, 0.4);
    border-radius: 1.5rem;
}
.news_input::placeholder {
    color: rgb(255, 255, 255);
}
.newsletter_button {
    font-weight: 500;
    font-size: 1.6rem;
    position: absolute;
    right: 0;
    display: block;
    top: 0;
    height: 100%;
    width: 6rem;
    appearance: none;
    outline: 0;
    background: transparent;
    border: 0;
    cursor: pointer;
    color:rgb(255, 255, 255);
    transition: color 300ms ease;
}
.newsletter_button:hover {
    color: rgb(12, 11, 49);
}

.rightSide {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
}

.rUp {
    display: flex;
    flex-direction: row;
    height: 20%;
    width: 100%;
    font-family: 'Lobster', cursive;
    font-size: 2.3em;
    color: rgb(246, 246, 246);
    justify-content: flex-end;
}
.rUp > h1 {
    margin: 3% 10% 0 0;
}
.rLow {
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.socials {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    height: 100%;
    width: 100%;
    margin: 3% 9% 9% 9%;
}
.socials > a > ion-icon {
    transition: color 200ms ease;
}
.socials > a > ion-icon:hover {
    color: rgb(4, 6, 75);
}
.rBottom {
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: row;
    margin: 3% 9% 9% 9%;
}
.copyright {
    display: flex;
    justify-content: flex-end;
    position: relative;
    flex-direction: row;
    height: 100%;
    width: 100%;
    margin: 0 0 0 -20%;
}

.copyright > p {
    font-family: 'Manrope', sans-serif;
    color: #fff;
}