.About {
    height: 60rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(247, 246, 239);
    flex-direction: row;
}
.leftAbt {
    position: relative;
    height: 100%;
    width: 90%;
    background: rgb(247, 246, 239);
    display: flex;
    flex-direction: column;
}
.benefit {
    text-align: left;
    height: 40%;
    font-family: 'Manrope', sans-serif;
    font-weight: 500;
    font-size: 3.4em;
    color: rgb(11, 11, 68);
    margin: 2.7em 1% 1% 8%;
    display: flex;
}
.bullets {
    height: 30%;
    font-family: 'Manrope', sans-serif;
    font-size: 2.1vw;
    display: flex;
    flex-direction: column;
    text-align: left;
    text-indent: 3%;
    justify-content: center;
    padding: 1% 1% 1% 1%;
    color: rgb(11, 11, 68);
    margin: 1% 1% 1% 8%;
    transform: translate(-100%, 0%);
}
.About:hover > .leftAbt > .bullets {
    animation: bullet_load 1200ms ease-out forwards;
}
.abtCTA {
    height: 30%;
    width: 50%;
    font-family: 'Manrope', sans-serif;
    font-size: 1vw;
    font-weight: 500;
    text-decoration: none;
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 0 3% 8%;
}
.callToAction {
    text-decoration: none;
    color: rgb(11, 11, 68);
    font-size: 1.5em;
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    transition: color 200ms ease;
}
.callToAction:hover {
    color: rgb(149, 96, 129);
}
.callToAction:hover > ion-icon {
    color: rgb(149, 96, 129);
}
.callToAction > ion-icon {
    color: rgb(11, 11, 68);
    position: relative;
}
.rightAbt {
    position: relative;
    height: 100%;
    width: 25%;
    background: rgb(247, 246, 239);
}



@keyframes bullet_load {
    0% {
        transform: translate(-120%, 0%);
    }
    100% {
        transform: translate(0%, 0%);
    }
}