@-webkit-keyframes h1 {
    0% {
        -webkit-transform: rotate(0) translateX(0);
                transform: rotate(0) translateX(0);
    }
    100% {
        -webkit-transform: rotate(-35deg) translateX(-5%);
                transform: rotate(-35deg) translateX(-5%);
    }
}


@keyframes h1 {
    0% {
        -webkit-transform: rotate(0) translateX(0);
                transform: rotate(0) translateX(0);
    }
    100% {
        -webkit-transform: rotate(-35deg) translateX(-5%);
                transform: rotate(-35deg) translateX(-5%);
    }
}
@-webkit-keyframes h2 {
    0% {
        -webkit-transform: rotate(0) translateY(0) translateX(0);
                transform: rotate(0) translateY(0) translateX(0);
    }
    100% {
        -webkit-transform: rotate(-28deg) translateY(-26%) translateX(0%);
                transform: rotate(-28deg) translateY(-26%) translateX(0%);
    }
}
@keyframes h2 {
    0% {
        -webkit-transform: rotate(0) translateY(0) translateX(0);
                transform: rotate(0) translateY(0) translateX(0);
    }
    100% {
        -webkit-transform: rotate(-28deg) translateY(-26%) translateX(0%);
                transform: rotate(-28deg) translateY(-26%) translateX(0%);
    }
}
@-webkit-keyframes h3 {
    0% {
        -webkit-transform: rotate(0) translateY(0) translateX(0);
                transform: rotate(0) translateY(0) translateX(0);
    }
    100% {
        -webkit-transform: rotate(-14deg) translateY(-44%) translateX(5%);
                transform: rotate(-14deg) translateY(-44%) translateX(5%);
    }
}
