.FAQ {
    position: relative;
    width: 100%;
    height: 60rem;
    background-image: linear-gradient(to top, #3b41c5 0%, #a981bb 49%, #ffc8a9 100%);
    display: flex;
    flex-direction: column;
}

.title {
    width: 100%;
    height: 10%;
    display: flex;
    justify-content: center;
    font-family: 'Manrope', sans-serif;
    font-size: 1.7em;
    color: #fff;
    position: relative;
    margin: 1em 0 0 0;
}
.title::after {
    content: '';
    position: absolute;
    height: 6px;
    width: 130px;
    background: #fff;
    border-radius: 15px;
    top: 55%;
    -webkit-transform: translate(0%, 0%);
            transform: translate(0%, 0%);
    margin: .6em 0 0 0;
}

.mainFAQ {
    display: flex;
    height: 100%;
    width: 100%;
    flex-direction: column;
    background: #fff; 
}
.innerFAQ {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
}
.leftFAQ:hover > .bulletFAQ {
    -webkit-animation: faq_load 1200ms ease-out forwards;
            animation: faq_load 1200ms ease-out forwards;
}
.leftFAQ {
    display: flex;
    flex-direction: row;
    height: 100%;
    width: 100%;
}
.bulletFAQ {
    height: 100%;
    display: flex;
    flex-direction: column;
    margin: 0 0 0 5%;
    font-family: 'Manrope', sans-serif;
    justify-content: center;
    text-align: left;
    color: rgb(11, 11, 68);
    margin: 3% 10% 10% 8%;
    -webkit-transform: translate(-120%, 0%);
            transform: translate(-120%, 0%);
}
.bulletFAQ > h1 {

}
.bulletFAQ > p {
    font-size: 1.2vw;
    padding: .5% 0 5% 0;
}
.rightFAQ {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 30%;
}

@-webkit-keyframes faq_load {
    0% {
        -webkit-transform: translate(-120%, 0%);
                transform: translate(-120%, 0%);
    }
    100% {
        -webkit-transform: translate(0%, 0%);
                transform: translate(0%, 0%);
    }
}

@keyframes faq_load {
    0% {
        -webkit-transform: translate(-120%, 0%);
                transform: translate(-120%, 0%);
    }
    100% {
        -webkit-transform: translate(0%, 0%);
                transform: translate(0%, 0%);
    }
}

.Footer {
    position: absolute;
    height: 35rem;
    width: 100%;
    z-index: 5;
    background: #3b41c5;
    display: flex;
    flex-direction: row;
}

.leftSide {
    height: 100%;
    width: 70%;
    display: flex;
    flex-direction: column;
}
.lUpper {
    height: 40%;
    width: 100%;
    display: flex;
    flex-direction: row;
    font-family: 'Manrope', sans-serif;
}
.leftUp {
    height: 100%;
    width: 33.3%;
    display: flex;
    flex-direction: column;
    padding: 50px 0 0 0;
}
.leftUp > a:hover, .midUp > a:hover {
    color: rgb(12, 11, 49);
}
.leftUp > a, .midUp > a {
    transition: color 150ms ease;
}
.midUp {
    height: 100%;
    width: 33.3%;
    display: flex;
    flex-direction: column;
    padding: 50px 0 0 0;
}
.rightUp {
    height: 100%;
    width: 33.3%;
    display: flex;
    flex-direction: column;
    padding: 50px 0 0 0;
}
.leftUp, .midUp, .rightUp > h1 {
    color: #fff;
}
a {
    margin: 15px 0 0 0;
    color: rgb(255, 255, 255);
    text-decoration: none;
}

.lLower {
    height: 60%;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    font-family: 'Manrope', sans-serif;
    color: rgb(255, 255, 255);
}
.upUpper {
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    font-family: 'Manrope', sans-serif;
    color: rgb(255, 255, 255);
    margin: 0 0 0 9%;
}
.lowUpper {
    height: 0%;
    width: 100%;
    display: flex;
    align-items: center;
    margin: 0 0 0 9%;
}

.buttonBox {
    border-radius: 1.5rem;
    border: 0px solid #e3e3e3;
    overflow: hidden;
}
.signUpForm {
    width: 400px;
    display: flex;
    flex-direction: row;
    position: relative;
    border-radius: 1.5rem;
    font-family: 'Manrope', sans-serif;
}
.news_input {
    width: 100%;
    background: 0 0;
    border: 1px solid #e3e3e3;
    border-radius: 1.5rem;
    font-size: 1.6rem;
    line-height: 81%;
    color: #fff;
    padding: 1.5rem 6rem 1.5rem 2.3rem;
    font-family: lausanne,sans-serif;
    transition: 200ms ease;
}
.news_input:focus {
    outline-color: rgb(34, 33, 78);
    box-shadow: 0 0 0 4px rgba(21, 156, 228, 0.4);
    border-radius: 1.5rem;
}
.news_input::-webkit-input-placeholder {
    color: rgb(255, 255, 255);
}
.news_input:-ms-input-placeholder {
    color: rgb(255, 255, 255);
}
.news_input::placeholder {
    color: rgb(255, 255, 255);
}
.newsletter_button {
    font-weight: 500;
    font-size: 1.6rem;
    position: absolute;
    right: 0;
    display: block;
    top: 0;
    height: 100%;
    width: 6rem;
    -webkit-appearance: none;
            appearance: none;
    outline: 0;
    background: transparent;
    border: 0;
    cursor: pointer;
    color:rgb(255, 255, 255);
    transition: color 300ms ease;
}
.newsletter_button:hover {
    color: rgb(12, 11, 49);
}

.rightSide {
    height: 100%;
    width: 50%;
    display: flex;
    flex-direction: column;
}

.rUp {
    display: flex;
    flex-direction: row;
    height: 20%;
    width: 100%;
    font-family: 'Lobster', cursive;
    font-size: 2.3em;
    color: rgb(246, 246, 246);
    justify-content: flex-end;
}
.rUp > h1 {
    margin: 3% 10% 0 0;
}
.rLow {
    height: 50%;
    width: 100%;
    display: flex;
    flex-direction: row;
}
.socials {
    display: flex;
    justify-content: flex-end;
    flex-direction: row;
    height: 100%;
    width: 100%;
    margin: 3% 9% 9% 9%;
}
.socials > a > ion-icon {
