
.Nav {
    margin-top: 10px;
    position: absolute;
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: row;
    align-items: center;
    animation: nav-load 600ms ease-out;
    z-index: 5;
}

.Nav-transition {

}
.logo {
    color: rgb(255, 255, 255);
    position: absolute;
    left: 50%;
    font-size: 1.9em;
    transform: translate(-50%);
    font-family: 'Lobster', cursive;
}

.leftLinks {
    margin: 5%;
}
.leftLinks > a {
    transition: color 200ms ease;
}
.leftLinks > a:hover {
    color: rgb(14, 7, 121);

}
.buttons {
    position: absolute;
    left: 90%;
    transform: translate(-50%);
    display: flex;
    flex-direction: row;
}
.signIn {
    padding: 0.4rem 1rem;
    background: #b376a800;
    border: 2px solid #fafafa;
    font-size: 1rem;
    color: #fff;
    line-height: 1;
    border-radius: 25px;
    outline: none;
    cursor: pointer;
    transition: 0.3s;
    margin: 10px;
    font-family: 'Rubik', sans-serif;
}
.signIn:hover {
    border: 2px solid #030569;
    color: #030569;
}
ion-icon {
    margin: 15px;
    font-size: 32px;
    cursor: pointer;
    color: #fff;
}
.buttons > ion-icon:hover {
    color: #030569;
}
@keyframes nav-load {
    0% {
        transform: translateY(-100%);
    }
    100% {
        transform: translateY(0);
    }
}